import React from 'react';
import s from '../HomeMain/Home.module.css'
import {NavLink,Route} from "react-router-dom";
const HomeFaq = () => {
    return <><div className={s.faq}>
        <div>
            <h4>
                <div>  FAQ,</div>
                <div>  ,    &rarr;</div>
                <div className={s.backHome}>   <NavLink to={'/home'}>&#11148; Home</NavLink></div>
            </h4>
        </div>
        <div className={s.listPages}>

            {/*<NavLink to={'/FAQ'}>&#11148; Back</NavLink>*/}
            <NavLink to={'/FAQ/profile'}>Profile &#9660;</NavLink>
            <NavLink to={'/FAQ/dialogs'}>Dialogs &#9660;</NavLink>
            <NavLink to={'/FAQ/users'}>Users &#9660;</NavLink>
        </div>

    </div>
    <div>
        <Route path={'/FAQ/profile'} render={() => <div>profile</div>}/>
        <Route path={'/FAQ/dialogs'} render={() => <div>dialogs</div>}/>
        <Route path={'/FAQ/users'} render={() => <div>users</div>}/>
    </div>
    </>
}
export default HomeFaq;