import React from 'react';
import {Header} from "./header";
import {connect} from "react-redux";
import {LogoutT, SetUser} from "../../../redux/Auth/AuthReducer";
import {GetMyProfile} from "../../../redux/Profile/ProfilePageReducers/ProfilePageReducer";

class HeaderContainerAPI extends React.Component{
	render(){
		return <>
			<Header {...this.props}/>
		</>
	}
}
let MapStateToProps = (state) => {
	return{
		isAuth:state.auth.isAuth,
		MyProfile:state.ProfilePage.MyUser,
		login:state.auth.login,
	}
}
const HeaderContainer = connect(MapStateToProps,{SetUser,LogoutT,GetMyProfile})(HeaderContainerAPI)
export {HeaderContainer};
